// Styling for the masthead
header.masthead {
	// TIP: Background images are set within the HTML using inline CSS!
	margin-bottom: 30px;
	background: no-repeat center center;
	background-color: $gray-600;
	background-attachment: scroll;
	position: relative;
	@include background-cover;
	.overlay {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		background-color: $gray-900;
		opacity: 0.5;
	}
	.page-heading,
	.post-heading,
	.site-heading {
		padding: 200px 0 150px;
		color: white;
		@media only screen and (min-width: 768px) {
			padding: 200px 0;
		}
	}
	.page-heading,
	.site-heading {
		text-align: center;
		h1 {
			margin-top: 600px;
			font-size: 35px;
			font-weight: 1200;
			line-height: 0.9;
			text-shadow: 4px 2px 2px rgba(44, 35, 35, 0.6);
			font-stretch: condensed;
		}
		.subheading {
			font-size: 22px;
			font-weight: 350;
			line-height: 0.95;
			display: block;
			margin: 10px 0 0;
			text-shadow: 3px 2px 2px rgba(44, 35, 35, 0.6);
			@include sans-serif-font;
		}
		@media only screen and (min-width: 768px) {
			h1 {
				font-size: 80px;
			}
		}
	}
	.post-heading {
		text-align: center;
		h1 {
			font-size: 35px;
			text-shadow: 3px 2px 2px rgba(44, 35, 35, 0.6);
		}
		.meta,
		.subheading {
			line-height: 1.1;
			display: block;
			text-shadow: 2px 1px 1px rgba(44, 35, 35, 0.6);
		}
		.subheading {
			font-size: 24px;
			font-weight: 600;
			margin: 10px 0 30px;
			@include sans-serif-font;
		}
		.meta {
			font-size: 20px;
			font-weight: 300;
			font-style: italic;
			@include serif-font;
			a {
				color: $white;
			}
		}
		@media only screen and (min-width: 768px) {
			h1 {
				font-size: 55px;
			}
			.subheading {
				font-size: 30px;
			}
		}
	}
}
